nav {
 color : #fff;
 display: flex;
 padding: 0.4rem;
 background: rgba(0, 0, 0, 0.8);
 position: fixed;
 width: 100vw;
 z-index: 999;
}

.nav-container{
    margin-left: auto;
    margin-right: 2rem;
    display: flex;
    opacity: 1;
    padding: 0.4rem;
    transition: opacity 0.3s ease-in-out;
} 


.nav-heading{
    margin-left: 37%;
    letter-spacing: 4px;
    font-size: 2em;
    margin-right: 2rem;
    /* background: violet; */
}

.nav-bar::before{
    height: 0.2rem;
    background: #e67e22;
    position: absolute;
    bottom: 0.4rem;
    content: " ";
    width: 100%;
    
}

.nav-bar{
    height: 0.2rem;
    background: #e67e22;
    margin: 1rem;
    position: absolute;
    top: 20%;
    right: 2rem;
    content: " ";
    width: 1.5rem;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
}

.nav-bar::after{
    height: 0.2rem;
    background: #e67e22;
    position: absolute;
    top: 0.4rem;
    content: " ";
    width: 100%;
}

.nav-container li{
    list-style: none;
    margin-right: 1.5rem;
    margin-bottom: 2px;
    transition: 0.2s ease;
} 

.nav-container li:hover{
    border-bottom: 0.2rem solid #e67e22;
}

.nav-items {
    color: #fff;
    text-decoration: none;
} 


    

@media screen and (max-width: 990px) {
    .nav-container{
        opacity: 0;
    }

    .nav-bar{
        opacity: 1;
    }

    .nav-bar:hover ~ .nav-container{
        background: blue;
        position: absolute;
        top: 100%;
        width: 100%;
        margin-left: 0;
        text-align: center;
    }
    
    .nav-container li{
        list-style: none;
        padding: 0.7rem;
        font-weight: 600;
        font-size: 110%;
        transition: 0.2s ease;
    }
}